export default {
  // 父子通信
  props: {
    postObj: {
      type: Object,
      default: function(){
      	return {};
      }
    },
	postType:{
		type: Number,
		default: 0,	// 0 -- 默认  1 -- 自己作品
	},
	showPostOpt: {
      type: Boolean,
      default: true,
    },
  },
  components : {
  },
  data () {
    return {
    };
  },
  mounted () {

  },
  computed: {
    isLogin () {
	  return true;
    },
  },
  methods: {
    // 点击分享
    sharePost (item) {
      let sharePopShow = true;
      let itemIsTop = true;
      this.$emit('on-share-post', sharePopShow, itemIsTop)
    },
    // 加关注
    addFollow: function (item) {
      let that = this;
	  const query = that.Bmob.Query('postList')
	  query.get(item.objectId).then(res => {
	      res.increment('view')
	      res.save()
	  }).catch(err => {
	      console.log(err)
	  })
    },
	// 删除帖子
	deletePost: function (item) {
		let that = this;
		uni.showModal({
		    title: '提示',
		    content: '删除后，所有相关的信息都将被删除，是否删除？',
		    success: function (res) {
		        if (res.confirm) {
		            that.$emit('on-delete-post')
		        } else if (res.cancel) {
		            console.log('用户点击取消');
		        }
		    }
		});
    },
    // 图片预览
    preImg(index) {
    	uni.previewImage({
    		current: this.postObj.images[index],
    		urls: this.postObj.images
    	})
    },
    // 前往用户主页
    toUserZone () {
      this.$router.push({ name: 'userzone', params: '' })
    },
    // 前往帖子分类
    toPostClassify (item) {
      this.$router.push({ name: 'postClassify', params: { containerid: item.containerid }})
    },
  }
};
